@font-face {
  font-family: "gteestipro";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/gteestiprodisplay_thin.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "gteestipro";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/gteestiprodisplay_regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "gteestipro";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/gteestiprodisplay_medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "steelfish";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/steelfish.woff2") format("woff2");
  font-display: swap;
}
html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  color: #e8e8e8;
  background-color: #26282d;
  font-family: "gteestipro", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 150%;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
}

a {
  color: #aad28c;
  text-decoration: none;
  transition: all 0.3s;
}

a:hover {
  color: #aad28c;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

address {
  font-style: normal;
}

button {
  font-size: 18px;
  background-color: transparent;
  border: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

input,
textarea,
button {
  font-family: "gteestipro", "Arial", sans-serif;
}

textarea {
  height: 100%;
}

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.clear-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.body-lock {
  overflow: hidden;
}
@media (min-width: 1024px) {
  .body-lock {
    overflow: visible;
  }
}

.round-image {
  overflow: hidden;
  border-radius: 6px;
}

.container {
  margin: 0 20px;
}
@media (min-width: 1280px) {
  .container {
    margin: 0 auto;
    max-width: 1320px;
    padding: 0 5px;
    box-sizing: border-box;
  }
}

.header {
  background-color: #242429;
  animation: 0.7s ease-out 0s header;
}
.header--sticky {
  position: sticky;
  top: 0;
  z-index: 5;
}
.header__container {
  height: 60px;
  display: flex;
  align-items: center;
}
@media (min-width: 1280px) {
  .header__container {
    height: 80px;
  }
}
.header__button {
  margin-left: auto;
}
@media (min-width: 1024px) {
  .header__menu {
    display: flex;
    gap: 24px;
    margin-left: 6%;
  }
}
@media (min-width: 1280px) {
  .header__menu {
    gap: 32px;
  }
}
.header__link {
  color: #fbfbff;
}

@keyframes header {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
.burger {
  width: 24px;
  height: 24px;
  position: relative;
  z-index: 7;
  overflow: hidden;
  margin-right: 24px;
}
@media (min-width: 1024px) {
  .burger {
    display: none;
  }
}
.burger::before, .burger::after {
  content: "";
  width: 22px;
  height: 2px;
  background-color: #fbfbff;
  position: absolute;
  transition: all 0.5s;
}
.burger::before {
  bottom: 14px;
}
.burger::after {
  bottom: 6px;
}
.burger--open::before {
  bottom: 12px;
  transform: rotate(45deg);
}
.burger--open::after {
  bottom: 12px;
  transform: rotate(-45deg);
}

@media (max-width: 1023px) {
  .burger-panel {
    position: fixed;
    top: 60px;
    left: 0;
    width: 220px;
    padding: 20px 50px;
    background: #242429;
    transform: translateX(-100%);
    transition: all 0.3s;
    box-sizing: border-box;
    overflow-y: scroll;
    z-index: 2;
  }
  .burger-panel.active {
    transform: translateX(0);
    z-index: 6;
  }
}
.logo {
  color: #fbfbff;
  background-color: #26282d;
  padding: 6px 16px;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 1.2;
}
.logo span {
  font-weight: 600;
}

.link {
  color: #aad28c;
  transition: all 0.3s;
}
.link:hover {
  color: #d2b148;
}
.link:active {
  color: #fbfbff;
}

.title {
  font-size: 56px;
  color: #fbfbff;
  text-transform: uppercase;
  font-family: "steelfish", "Arial", sans-serif;
  font-weight: 400;
  line-height: 105%;
  margin: 0;
  transform: translateY(80px);
  transition: all ease-in 1s;
  transition-delay: 300ms;
  opacity: 0;
}
.title._active {
  transform: translateY(0);
  opacity: 1;
}
@media (min-width: 1024px) {
  .title {
    font-size: 90px;
    display: grid;
  }
}
@media (min-width: 1280px) {
  .title {
    font-size: 128px;
  }
}
.title__accent {
  color: #aad28c;
}

.hero {
  padding-top: 64px;
}
@media (min-width: 1280px) {
  .hero {
    padding-top: 56px;
  }
}
.hero__container {
  position: relative;
}
@media (min-width: 1024px) {
  .hero__container {
    display: grid;
    grid-template-columns: 6fr 3fr;
    gap: 56px;
  }
}
.hero__container::before {
  content: "";
  width: 40px;
  height: 40px;
  position: absolute;
  top: -20px;
  right: 40px;
  z-index: 1;
  background-image: url("../img/star.svg");
  background-repeat: no-repeat;
  opacity: 0.1;
  transition: all 0.9s;
  transform: translateY(20px);
}
.hero__container._active::before {
  opacity: 1;
  transition-delay: 200ms;
  transform: translateY(0);
}
.hero__content {
  position: relative;
}
.hero__content::after {
  content: "";
  width: 60px;
  height: 60px;
  position: absolute;
  top: 35%;
  left: 0;
  z-index: 1;
  background-image: url("../img/arrow.svg");
  background-repeat: no-repeat;
  background-size: contain;
  opacity: 0;
  transition: all 0.9s;
  transform: translateY(30px);
}
@media (min-width: 1024px) {
  .hero__content::after {
    top: 62%;
  }
}
@media (min-width: 1280px) {
  .hero__content::after {
    width: 80px;
    height: 80px;
  }
}
.hero__content._active::after {
  opacity: 1;
  transition-delay: 1200ms;
  transform: translateY(0);
}
.hero__second-title {
  margin-left: 80px;
}
@media (min-width: 1024px) {
  .hero__second-title {
    text-align: right;
  }
}
.hero__subtitle {
  color: #fbfbff;
  margin: 0;
  margin-top: 16px;
  margin-left: 80px;
  font-weight: 300;
  font-style: italic;
  font-size: 20px;
  line-height: 1.4;
  transition: all 0.9s;
  transform: translateY(90px);
  opacity: 0;
}
.hero__subtitle._active {
  transform: translateY(0);
  opacity: 1;
  transition-delay: 0.6s;
}
@media (min-width: 1024px) {
  .hero__subtitle {
    position: absolute;
    right: 0;
    top: 0;
  }
}
@media (min-width: 1280px) {
  .hero__subtitle {
    font-size: 26px;
  }
}
.hero__image {
  display: none;
  transition: all 0.9s;
  transform: translateY(200px);
  opacity: 0;
}
.hero__image._active {
  transform: translateY(0);
  opacity: 1;
  transition-delay: 0.2s;
}
@media (min-width: 1024px) {
  .hero__image {
    display: block;
    width: 220px;
    position: absolute;
  }
}
@media (min-width: 1280px) {
  .hero__image {
    width: 300px;
  }
}

.bullets {
  padding-top: 48px;
}
@media (min-width: 1024px) {
  .bullets {
    padding-top: 72px;
  }
}
.bullets__product {
  border-radius: 6px;
  overflow: hidden;
}
.bullets__content {
  position: relative;
}
.bullets__list {
  background-color: #242429;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  padding: 36px;
}
@media (min-width: 1024px) {
  .bullets__list {
    grid-template-columns: repeat(4, 1fr);
  }
}
.bullets__item {
  font-size: 16px;
  line-height: 1.2;
  opacity: 0;
  transition: all 1s;
  transform: translateY(10px);
}
@media (min-width: 1024px) {
  .bullets__item {
    text-align: center;
  }
}
.bullets__item._active {
  opacity: 1;
  transform: translateY(0);
}
.bullets__item:nth-child(1) {
  transition-delay: 0.2s;
}
.bullets__item:nth-child(2) {
  transition-delay: 0.4s;
}
.bullets__item:nth-child(3) {
  transition-delay: 0.6s;
}
.bullets__item:nth-child(4) {
  transition-delay: 0.8s;
}

