}
.protect__item:nth-child(3) {
  transition-delay: 0.6s;
}
.protect__item:nth-child(4) {
  transition-delay: 0.8s;
}
.protect__item:nth-child(4) {
  transition-delay: 1s;
}
.protect__item + .protect__item {
  margin-top: 36px;
}
.protect__subtitle {
  color: #aad28c;
  font-size: 24px;
