}
@media (min-width: 1024px) {
  .protect__image {
    margin-top: 0;
    margin-bottom: 0;
  }
}
.protect__image--header {
  opacity: 0;
  transition: all 1s;
  transform: translateX(140px);
}
@media (min-width: 1024px) {
  .protect__image--header {
    position: absolute;
    right: 90px;
    bottom: 20px;
    width: 270px;
  }
}
@media (min-width: 1280px) {
