  .modal__content--video iframe {
    width: 100%;
    height: 450px;
  }
}
.modal__close-btn {
  position: absolute;
  right: -8px;
  top: -40px;
  color: #e8e8e8;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal__close-btn svg {
  display: block;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: auto;
}
